import React from 'react';
import '../style/main.scss';
import Header from '../components/Header';
import Footer from '../components/Footer';
import Subscription from '../components/Subscription';
import Slide from 'react-reveal/Slide'
import CountryCode from '../common/CountryCode';
import {ExploreProduct, TeamImage, Devicess, Pedalogy, TeamImage1, TeamImage2, TeamImage3} from '../common/Icons';
import SEO from "../components/seo"



class AboutUs extends React.Component{
    state={
        facultyImg: 0,
        countries:{}
    }

    componentDidMount(){
        CountryCode().then(data => {
            this.setState({ countries: data })
            // console.log(data)
        }).then(e => console.log(e))
    }

    onBackButtonClick=(e)=> {
        if (this.state.facultyImg !== 0 && this.state.facultyImg <= 3) {
            this.setState({ facultyImg: this.state.facultyImg - 1 })
        } else if (this.state.facultyImg === 0) {
            this.setState({ facultyImg: 3 })
        }
    }

    onNextButtonClick = (e) => {
        if (this.state.facultyImg < 3) {
            this.setState({ facultyImg: this.state.facultyImg + 1 })
        } else if (this.state.facultyImg === 3) {
            this.setState({ facultyImg: 0 })
        }
    }

    render(){
        const facultyImg = [TeamImage, TeamImage1, TeamImage2, TeamImage3];
        return (
            <main>
                <SEO title="About Us" />
                <div className="bg-theme">
                    <div className="header-secondary"><Header/></div>
                    <div className="head-banner">
                        <div className="container">
                            <div className="row">
                                <div className="col-xl-9 col-lg-12 col-md-12 float-none m-auto text-center">
                                    <h1 className="color-white">Our Mission <br /> ‘Make Learning Personalised’</h1>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="section-spacing pB0 text-md-center">
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-5 col-lg-7 col-md-12">
                                <div className="section-heading">
                                    <h2>What do we do?</h2>
                                </div>
                                <p className="text-justify mT20">At, OnePrep we follow a comprehensive approach
                                to help you cover all the topics in a concise format. We make learning easier for
                                you with the help of our courses that include all the topics that are needed to
                                be covered to get you exam ready in no time. Get complete access to HD videos
                                by our Dream Team along with a bagful of questions from previous years’ examinations
                               and a lot more. All this is available for you at just a click away. </p>
                                <a href="" className="mT20"><u>EXPLORE OUR PRODUCT</u></a>
                            </div>
                            <div className="col-xl-6 col-lg-5 col-xs-12 text-right d-md-none">
                                <img src={ExploreProduct} />
                            </div>
                        </div>
                    </div>
                </div>

                <div className="section-spacing team md-mt-30">
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-6 col-lg-7 col-md-12 float-none m-auto">
                                <div className="section-heading text-center">
                                    <h2>Prepare with the best Faculty</h2>
                                    <p>With a mission to make learning accessibly to everyone, anytime,
                                    anywhere; we present to you OnePrep’s Dream Team.
                                </p>
                                </div>
                            </div>
                        </div>
                        <div className="row mT60 md-mt-30">
                            <div className="col-xl-3 col-lg-4 col-md-12 text-center">
                                <div style={{ overflow: "hidden" }} className="team-img">
                                    {
                                        facultyImg && facultyImg.length &&
                                        facultyImg.map((img, i) => (
                                            <Slide left duration={500} key={i} >
                                                <img className={`team-img_faculty ${this.state.facultyImg===i ? '': 'd-none'}`} src={img} />
                                            </Slide>
                                        ))
                                    }
                                </div>
                                <div className="team-member f-20 color-heading mT10"><strong>Phillip Anthropy</strong></div>
                                <div className="team-member-designation f-14">web designer</div>

                            </div>
                            <div className="col-xl-9 col-lg-8 col-md-12 md-mt-20 text-md-center">
                                <p className="text-justify">At Toppr, students are at the centre of our universe. We believe that every student is different
                                and has different learning needs. We work relentlessly to solve student problems using advanced
                                technology and help them learn better. We have thoughtfully designed Toppr to ensure that it
                                is a one-stop after-school learning platform. It has more than 1.5 million learning pieces,
                                including adaptive practice questions, solutions, concepts, and videos that come together
                                seamlessly to give 360° learning. We use artificial intelligence based machine learning
                                algorithms on top of this strong infrastructure, to create personalised learning paths for
                                millions of students.</p>
                                <ul className="list-inline pagination mT30">
                                    <li onClick={(e)=>this.onBackButtonClick(e)}><a className="arrow arrow-left"></a></li>
                                    <li onClick={(e)=>this.onNextButtonClick(e)} class="active"><a className="arrow arrow-right"></a></li>
                                </ul>
                                <a href="" className="btn btn-green mT30">View All</a>
                            </div>
                        </div>
                    </div>
                </div>

                <Subscription
                countryCode={this.state.countries.response}
                />
                <Footer />
            </main>
        );
    }
   
};

export default AboutUs;